import styled from "@emotion/styled";
import { motion } from "framer-motion";

type Props = {
  show: boolean;
};
const text =
  "     26 : 74     . ,         .";
const textArray = text.split("");
//      26 : 74
//     .
//          .
export const ChartDescription = ({ show }: Props) => {
  const makeText = (item: string, index: number) => {
    if (index === 28 || index === 50) return <p key={index}></p>;

    return (
      <Text
        key={index}
        animate={{
          opacity: show ? [0, 1] : 0,
          textShadow: show
            ? "white 1px 1px 5px"
            : "text-shadow: white 0px 0px 0px",
        }}
        transition={{
          duration: show ? 1 : undefined,
          delay: show ? 1.2 + index * 0.05 : undefined
          }}
      >
        {item}
      </Text>
    );
  };
  return (
    <Wrapper>{textArray.map((item, index) => makeText(item, index))}</Wrapper>
  );
};

const Wrapper = styled(motion.div)`
  margin-left : 10vw;
  width: 100%;
`;
const Text = styled(motion.span)`
  color: white;
  font-size: 2vw;
  font-weight: bold;
  opacity: 0;
`;
